import { AppBar, Box } from "@mui/material";
import React from "react";

const Footer = () => {
    return (
        <>
            <AppBar
                position="static"
                sx={{
                    mt: 6,
                    backgroundColor: "#CCCCCC",
                    height: "64px",
                }}
            >
                <Box
                    color={"black"}
                    sx={{
                        textAlign: "right",
                        ml: "auto",
                    }}
                    mr={2}
                    mt={1}
                >
                    版本：v1.1
                    <br />
                    最後更新時間：2024/8/29
                </Box>
            </AppBar>
        </>
    );
};

export default Footer;
